import PersianSupportNumericTextField from 'Global/Components/UnEditableComponents/PersianSupportNumericTextField/PersianSupportNumericTextField';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import React from 'react';

const MobileNumber = ({
  onChange,
  errors,
  name,
  disabled,
}) => {
  const mobile_number = FindLangKey(
    'general-public-mobile-number'
  );

  return (
    <React.Fragment>
      <PersianSupportNumericTextField
        isOctalAllowed={true}
        label={mobile_number}
        onChange={onChange}
        errors={errors}
        name={name}
        disabled={disabled}
      />
    </React.Fragment>
  );
};

export default MobileNumber;
