import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import { HandleGetVerification } from './HandleGetVerification';

export const HandleCheckPassword = (
  data,
  password_mismatch,
  setOTP
) => {
  if (data.Password !== data.ConfrimPassword) {
    SwalAlert('warning', password_mismatch, false);
    return;
  }
  HandleGetVerification(data, setOTP);
};
