import PersianSupportTextField from 'Global/Components/UnEditableComponents/PersianSupportTextField/PersianSupportTextField';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import React from 'react';

const EmailField = ({
  onChange,
  errors,
  name,
  disabled,
}) => {
  const email_field = FindLangKey('signup-input-email');

  return (
    <React.Fragment>
      <PersianSupportTextField
        onChange={onChange}
        errors={errors}
        name={name}
        label={email_field}
        disabled={disabled}
      />
    </React.Fragment>
  );
};

export default EmailField;
