import React, {
  useEffect,
  useLayoutEffect,
  useMemo,
  useState,
} from 'react';
import { RegisterUser } from '../adapters/RegisterUser';
import { useNavigate } from 'react-router-dom';

const useHandleRegisterUser = (getValues) => {
  const [enableRegister, setEnableRegister] =
    useState(false);

  const [values, setValues] = useState(null);

  const navigate = useNavigate();

  const vals = useMemo(
    () => values,
    [JSON.stringify(values)]
  );

  useEffect(() => {
    if (vals?.VerificationCode) {
      console.log('run ! ');
      RegisterUser(vals, navigate);
    }
  }, [vals?.VerificationCode]);

  useEffect(() => {
    if (enableRegister) {
      const data = getValues();
      data.CodeGozarname = data.CodeMelli;
      data.Username = data.CodeMelli;

      setValues(data);
    }
  }, [enableRegister]);

  return setEnableRegister;
};

export default useHandleRegisterUser;
