import PersianSupportNumericTextField from 'Global/Components/UnEditableComponents/PersianSupportNumericTextField/PersianSupportNumericTextField';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import React from 'react';

const NationalCodeNumeric = ({
  onChange,
  name,
  errors,
  disabled,
}) => {
  const national_code = FindLangKey(
    'signup-input-national-code-passport-id'
  );

  return (
    <React.Fragment>
      <PersianSupportNumericTextField
        isOctalAllowed={true}
        label={national_code}
        onChange={onChange}
        errors={errors}
        name={name}
        disabled={disabled}
      />
    </React.Fragment>
  );
};

export default NationalCodeNumeric;
