import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import { Post } from 'Xhr/Xhr';
import { env } from 'env';

export const RegisterUser = (data, navigate) => {
  Post(
    'POST',
    process.env.REACT_APP_API_URL_API_VERIFY_USER,
    false,
    {
      ...data,
    }
  ).then((response) => {
    if (response.data.resCode === 1) {
      SwalAlert(
        'success',
        response.data.resMessage,
        false
      ).then(() => {
        navigate('/login');
      });
    } else {
      SwalAlert('error', response.data.resMessage, false);
    }
  });
};
