import PersianSupportTextField from 'Global/Components/UnEditableComponents/PersianSupportTextField/PersianSupportTextField';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import React from 'react';

const PasswordField = ({
  onChange,
  isRepeat = false,
  errors,
  name,
  disabled,
}) => {
  const password_msg = FindLangKey(
    'general-public-password-key'
  );
  const repeat_pass_msg = FindLangKey(
    'sign-up-input-repeat-password'
  );
  return (
    <React.Fragment>
      <PersianSupportTextField
        onChange={onChange}
        errors={errors}
        name={name}
        label={isRepeat ? repeat_pass_msg : password_msg}
        isPassword={true}
        disabled={disabled}
      />
    </React.Fragment>
  );
};

export default PasswordField;
