import { QrCode2 } from '@mui/icons-material';
import { Button, Container, Dialog, DialogTitle } from '@mui/material';
import { themeColors } from 'Global/JsxThemeColor/JsxThemeColor';
import React, { useEffect, useState } from 'react';
import { Post } from 'Xhr/Xhr';

const Authenticator = ({onChange}) => {

  const[openQrDialog,setOpenQrDialog] = useState(false); 
  const[qrBase64,setQrbase64] = useState();

  useEffect(()=>{

    if(openQrDialog){
        Post('POST',process.env.REACT_APP_API_URL_API_GOOGLE_AUTHENTICATOR_GET_TOKEN,false,{})
        .then((response)=>console.log('response is=>',response))
    }


  },[openQrDialog])

  return (
   <>
    <Button
      variant="contained"
      onClick={()=>setOpenQrDialog(prev=>!prev)}
      sx={{
        color: `${themeColors.ColorPrimary} !important`,
      }}
      fullWidth
      startIcon={
        <QrCode2 sx={{
            scale:'1.5'
        }}/>
      }
    >
      ورود کد با Authenticator
    </Button>
    <Dialog open={openQrDialog}>
        <DialogTitle>تصویر زیر را اسکن کنید</DialogTitle>
        <Container sx={{mt:'1em'}}>
            Hello
        </Container>
    </Dialog>
   </>
  );
};

export default Authenticator;
