import React, { useState } from 'react';
import FormElements from './components/FormElements/FormElements';
import { useForm } from 'react-hook-form';
import { default_values } from './var/default_values';
import {
  Container,
  LinearProgress,
  Stack,
} from '@mui/material';
import SubmitButton from 'Global/Components/UnEditableComponents/SubmitButton/SubmitButton';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import { HandleCheckPassword } from './functions/HandleCheckPassword';
import CountDownProgress from './components/CountDownProgress/CountDownProgress';
import OTP from './components/OTP/OTP';
import SingleReactHookFormWrapper from 'Global/Components/Generators/SingleReactHookFormWrapper/SingleReactHookFormWrapper';
import { RegisterUser } from './adapters/RegisterUser';
import { useNavigate } from 'react-router-dom';
import { HandleRegisterUser } from './functions/HandleRegisterUser';
import useHandleRegisterUser from './hooks/useHandleRegisterUser';
import Authenticator from './components/Authenticator/Authenticator';

const SignUp = () => {
  const [otp, setOTP] = useState(true);
  const [disabled, setDisabled] = useState(false);
  const navigate = useNavigate();

  // const [enableRegister, setEnableRegister] =
  //   useState(false);

  const submit_msg = FindLangKey(
    'sign-up-btn-get-sms-verification'
  );
  const password_mismatch = FindLangKey(
    'sign-up-alert-password-mismatch'
  );
  const register_user_msg = FindLangKey(
    'sign-up-navbar-title'
  );

  const { control, formState, handleSubmit, getValues } =
    useForm({
      defaultValues: default_values,
    });

  const setEnableRegister =
    useHandleRegisterUser(getValues);

  return (
    <React.Fragment>
      <Container>
        <form
          onSubmit={handleSubmit((data) => {
            data.CodeGozarname = data.CodeMelli;
            data.Username = data.CodeMelli;
            !otp
              ? HandleCheckPassword(
                  data,
                  password_mismatch,
                  setOTP
                )
              : HandleRegisterUser(data, navigate);
          })}
        >
          <FormElements
            control={control}
            formState={formState}
            disabled={disabled}
          />
          {/* <OTP /> */}
          {otp ? (
            <Stack marginTop=".5em" gap=".5em">
              <CountDownProgress
                setOTP={setOTP}
                setDisabled={setDisabled}
              />
              <SingleReactHookFormWrapper
                element={
                  <Stack>
                    <OTP
                      setEnableRegister={setEnableRegister}
                    />
                    <Authenticator />
                  </Stack>
                }
                errors={formState.errors}
                name="VerificationCode"
                control={control}
              />
            </Stack>
          ) : (
            <></>
          )}

          <div
            style={{
              marginTop: '.5em',
            }}
          >
            {!otp ? (
              <SubmitButton label={submit_msg} />
            ) : (
              <></>
            )}
          </div>
        </form>
      </Container>
    </React.Fragment>
  );
};

export default SignUp;
