import ToggleButtonCreator from "Global/Components/CommonComponents/ToggleButtonCreator/ToggleButtonCreator";
import FindLangKey from "Global/Functions/FindLangKey/FindLangKey";
import React from "react";

const NationalityToggle = ({ onChange }) => {
  const iranian_msg = FindLangKey("sign-up-radio-btn-Iranian");
  const foreign_msg = FindLangKey("sign-up-radio-btn-foreigner");

  const toggleArray = [
    {
      value: "Irani",
      icon: iranian_msg,
    },
    {
      value: "foreign",
      icon: foreign_msg,
    },
  ];

  return (
    <React.Fragment>
      <ToggleButtonCreator
        iconArray={toggleArray}
        onChange={onChange}
        defaultValue="Irani"
      />
    </React.Fragment>
  );
};

export default NationalityToggle;
