import React from 'react';
import { Stack } from '@mui/material';
import SingleReactHookFormWrapper from 'Global/Components/Generators/SingleReactHookFormWrapper/SingleReactHookFormWrapper';
import NationalityToggle from './NationalityToggle/NationalityToggle';
import NationalCodeNumeric from './NationalCodeNumeric/NationalCodeNumeric';
import MobileNumber from './MobileNumber/MobileNumber';
import EmailField from './EmailField/EmailField';
import PasswordField from './PasswordField/PasswordField';

const FormElements = (props) => {
  const { control, formState, disabled } = props;

  return (
    <React.Fragment>
      <Stack gap=".5em">
        <SingleReactHookFormWrapper
          control={control}
          element={<NationalityToggle />}
          name="NationalityType"
          errors={formState.errors}
          required={true}
        />
        <SingleReactHookFormWrapper
          control={control}
          element={
            <NationalCodeNumeric disabled={disabled} />
          }
          name="CodeMelli"
          errors={formState.errors}
          required={true}
        />
        <SingleReactHookFormWrapper
          control={control}
          element={<MobileNumber disabled={disabled} />}
          name="TellNo"
          errors={formState.errors}
          required={true}
        />
        <SingleReactHookFormWrapper
          control={control}
          element={<EmailField disabled={disabled} />}
          name="Email"
          errors={formState.errors}
        />
        <SingleReactHookFormWrapper
          control={control}
          element={<PasswordField disabled={disabled} />}
          name="Password"
          errors={formState.errors}
          required={true}
        />
        <SingleReactHookFormWrapper
          control={control}
          element={
            <PasswordField
              isRepeat={true}
              disabled={disabled}
            />
          }
          name="ConfrimPassword"
          errors={formState.errors}
          required={true}
        />
      </Stack>
    </React.Fragment>
  );
};

export default FormElements;
