import useDebounce from 'Global/Hooks/useDebounce/useDebounce';
import React, { useEffect, useState } from 'react';
import OtpInput from 'react-otp-input';
import { ConvertToEnglish } from 'utils/ConvertToEnglish';
import useCheckScreenWidth from 'Global/Hooks/useCheckScreenWidth';

const OTP = ({ onChange, setEnableRegister }) => {
  const screenWidth = useCheckScreenWidth(); // 1024 for Desktop


  

  const [value, set_Value] = useState(null);
  const verificationCode = useDebounce(value);

  useEffect(() => {
    if (verificationCode?.length === 5) {
      onChange(verificationCode);
      setEnableRegister(true);
    }
  }, [verificationCode]);

  return (
    <React.Fragment>
      <OtpInput
        value={value}
        onChange={(val) => set_Value(ConvertToEnglish(val))}
        autoFocus
        numInputs={5}
        otpType="number"
        disabled={false}
        inputStyle={{
          width: '4.8em',
          height: '3em',
          marginBottom: '1em',
        }}
        containerStyle={{
          display: 'flex',
          alignItems: 'center',
          width: '100%',
          justifyContent:screenWidth>1024?'space-between': 'center',
          gap:screenWidth>1024?0:'0.2em'
        }}
 
      />
    </React.Fragment>
  );
};

export default OTP;
