import React, { useEffect, useState } from 'react';
import { LinearProgress } from '@mui/material';
import useCountDown from 'Global/Hooks/useCountDown/useCountDown';
const CountDownProgress = ({ setOTP, setDisabled }) => {
  const { countDown, setStart } = useCountDown(100, 1000);

  useEffect(() => {
    setStart(true);
  }, []);

  useEffect(() => {
    if (countDown === 0) {
      setOTP(false);
      setDisabled(false);
    } else {
      setDisabled(true);
    }
  }, [countDown]);

  return (
    <React.Fragment>
      <div>
        <LinearProgress
          variant="determinate"
          value={countDown}
        />
      </div>
    </React.Fragment>
  );
};

export default CountDownProgress;
